import EditDialog from './edit-dialog.vue';
import TablePage from '@/components/tablePage';
import KycForm from '@/components/kyc-common/kyc-form/index.vue';
export default {
  name: 'groupDetail',
  props: {
    basicInfoForm: {
      type: Object,
      default() {
        return {};
      },
    },
    isShowVerify: {
      type: Boolean,
      default: false,
    },
    languageName: {
      type: String,
      default: '',
    },
    identityID: {
      type: String,
      default: '',
    },
  },
  components: { TablePage, EditDialog, KycForm },
  data() {
    return {
      titleConfig: {
        title: '',
        desc: this.$t('IDC.groupDetail.pageDesc'),
      },
      dialogType: '',
      submitLoading: false,
      currentPannel: this.$t('IDC.groupDetail.label.AccountInfo'), // 当前tab的激活项
      stateTagPage: [
        { label: 'Tag Name', prop: 'tagName', align: 'left', __slotName: 'tagName' },
        { label: 'Added Time', prop: 'addedTime', align: 'left', __slotName: 'addedTime' },
        { label: 'Auto Remove', prop: 'autoRemove', align: 'left', __slotName: 'autoRemove' },
        { label: 'Keep Duration', prop: 'keepDuration', align: 'left', __slotName: 'keepDuration' },
        { label: 'Keep Duration Unit', prop: 'keepDurationUnit', align: 'left', __slotName: 'keepDurationUnit' },
        { label: 'Auto Remove Time', prop: 'autoRemoveTime', align: 'left', __slotName: 'autoRemoveTime' },
        { label: 'Keep Duration', prop: 'keepDuration', align: 'left', __slotName: 'keepDuration' },
        { label: 'Reason', prop: 'reason', align: 'left', __slotName: 'reason' },
        {
          label: this.$t('common.label.operation'),
          prop: 'operation',
          align: 'left',
          __slotName: 'operation',
        },
      ],
      editRegisteredbyVisible: false,
      editLanguageVisible: false,
      editEditRuleProfileVisible: false,
      ruleProfile: [],
    };
  },
  created() {},
  activated() {},
  deactivated() {
    this.basicInfoForm = {};
  },
  computed: {
    basePath() {
      const type = this.getConfigRouteType();
      return `/config/${type}/commissionDistribution/`;
    },
  },
  methods: {
    showCustomerType(val) {
      switch (val) {
        case '1002':
          return this.$t('IDC.orgCustomer.registeredCustomer');
        case '1001':
          return this.$t('IDC.orgCustomer.liteCustomer');
        case '1000':
          return this.$t('IDC.orgCustomer.unregisteredCustomer');
        default:
          return;
      }
    },
    async getDetail() {
      this.submitLoading = true;
      let res = await queryCI({ ciId: this.basicInfoForm.id });
      if (res.header && res.header.success) {
      }
      this.submitLoading = false;
    },
    editRegisteredbyVisibleFn() {
      this.editRegisteredbyVisible = true;
    },
    editLanguageVisibleFn() {
      this.dialogType = 'language';
      this.editLanguageVisible = true;
    },
    closeEditLanguageDialog(data) {
      this.editLanguageVisible = data;
    },
    editRuleProfileVisibleFn() {
      this.dialogType = 'ruleProfile';
      this.ruleProfile = this.basicInfoForm.ruleProfileMap;
      this.editEditRuleProfileVisible = true;
    },
    closeEditRuleProfileDialog(data) {
      this.editEditRuleProfileVisible = data;
    },
    handleDelete() {},

    /**
     * 返回操作员列表
     */
    toReturn() {
      this.$tagTab.closeCurrentPage();
    },
    getTableData() {
      return new Promise(
        (res) => {
          res({
            header: {
              success: 'success',
            },
            list: [{}, {}],
          });
        },
        () => {}
      );
    },
  },
};
</script>

