<template>
  <div class="audit-log-page">
    <div class="basicDec">
      {{ $t('IDC.orgCustomer.basicDec') }}
    </div>
    <!-- Customer Beneficiary -->
    <div class="table-list-container">
      <div class="section-title">{{ $t('IDC.orgCustomer.customerBeneficiary') }}</div>
      <!-- 列表 -->
      <table-page
        class="margin-top-16"
        size="small"
        :has-pagination="true"
        :isSearch="isSearch"
        :columns="customerColumns"
        :pagination="paginationConfig"
        :getTableData="getCustomerTableData"
        :sortArr="['status', 'beneficiaryIdentifier', 'beneficiaryName', 'beneficiaryReference']"
      >
      </table-page>
    </div>
    <!-- Organization Beneficiary -->
    <div class="table-list-container">
      <div class="section-title">{{ $t('IDC.orgCustomer.organizationBeneficiary') }}</div>
      <!-- 列表 -->
      <table-page
        class="margin-top-16"
        size="small"
        :has-pagination="true"
        :isSearch="isSearch"
        :columns="organizationColumns"
        :pagination="paginationConfig"
        :getTableData="getOrganizationTableData"
        :sortArr="['status', 'beneficiaryIdentifier', 'beneficiaryName', 'billReference', 'beneficiaryReference']"
      >
      </table-page>
    </div>
    <!-- Group Beneficiary不做 -->
    <div class="table-list-container" v-if="false">
      <div class="section-title">{{ $t('IDC.orgCustomer.isShowVerify') }}</div>
      <!-- 列表 -->
      <table-page
        class="margin-top-16"
        size="small"
        :has-pagination="true"
        :isSearch="isSearch"
        :columns="groupColumns"
        :pagination="paginationConfig"
        :getTableData="getTableData"
        :sortArr="['status', 'roleId']"
      >
        <template slot-scope="scope" slot="operation">
          <el-button
            size="mini"
            type="text"
            class="table-operation-btn icon-cursor"
            @click="handleDetail(scope.data.row)"
          >
            {{ $t('common.button.detail') }}
          </el-button>
        </template>
      </table-page>
    </div>
  </div>
</template>

<script>
import TablePage from '@/components/tablePage';
import { lookUpBeneficiary } from '../../../../api/api-customer';

export default {
  name: 'beneficiaryList',
  props: {
    basicInfoForm: {
      type: Object,
      default() {
        return {};
      },
    },
    identityID: {
      type: String,
      default: '',
    },
  },
  components: {
    TablePage,
  },
  data() {
    return {
      closeData: {
        identityId: '',
        tillNumber: '',
      },
      closeDialog: false,
      identityStatus: '', // 弹框Current Identity Status
      total: 0,
      pageNo: 1,
      pageSize: 10,
      customerForm: {
        msisdn: '',
        tillNumber: '',
        status: '',
      },
      searchLoading: false,
      paginationConfig: {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
      },
      customerColumns: [
        {
          label: this.$t('MSISDN'),
          prop: 'beneficiaryIdentifier',
          'sort-method': (a, b) => {
            return a.beneficiaryIdentifier > b.beneficiaryIdentifier ? 1 : -1;
          },
          align: 'left',
        },
        {
          label: this.$t('Customer Name'),
          prop: 'beneficiaryName',
          'sort-method': (a, b) => {
            return a.beneficiaryName > b.beneficiaryName ? 1 : -1;
          },
          align: 'left',
        },
        {
          label: this.$t('Beneficiary Reference'),
          prop: 'beneficiaryReference',
          'sort-method': (a, b) => {
            return a.beneficiaryReference > b.beneficiaryReference ? 1 : -1;
          },
          align: 'left',
        },
      ],
      organizationColumns: [
        {
          label: this.$t('Organization Short Code'),
          prop: 'beneficiaryIdentifier',
          'sort-method': (a, b) => {
            return a.beneficiaryIdentifier > b.beneficiaryIdentifier ? 1 : -1;
          },
          align: 'left',
        },
        {
          label: this.$t('Organization Name'),
          prop: 'beneficiaryName',
          'sort-method': (a, b) => {
            return a.beneficiaryName > b.beneficiaryName ? 1 : -1;
          },
          align: 'left',
        },
        {
          label: this.$t('Bill Reference'),
          prop: 'billReference',
          'sort-method': (a, b) => {
            return a.billReference > b.billReference ? 1 : -1;
          },
          align: 'left',
        },
        {
          label: this.$t('Beneficiary Reference'),
          prop: 'beneficiaryReference',
          'sort-method': (a, b) => {
            return a.beneficiaryReference > b.beneficiaryReference ? 1 : -1;
          },
          align: 'left',
        },
      ],
      groupColumns: [
        { label: 'Group Code', prop: 'kycName', align: 'left' },
        { label: 'Group Name', prop: 'kycName', align: 'left' },
        { label: 'Beneficiary Reference', prop: 'kycName', align: 'left' },
      ],
      isSearch: false,
      collapseValue: true,
      statusOptions: [
        { label: 'All', value: '01' },
        { label: 'Active', value: '03' },
        { label: 'Closed', value: '06' },
        { label: 'Frozen', value: '05' },
        { label: 'Suspended', value: '00' },
      ],
    };
  },
  computed: {
    statusStyle() {
      return (status) => {
        switch (status) {
          case '03':
            return { 'background-color': '#658DF9' };
          case '06':
            return { 'background-color': `${this.GlobalStyleVariables.fillStrongColor}` };
          case '07':
            return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
          case '00':
            return { 'background-color': '#658DF9' };
          case '02':
            return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
          default:
            return;
        }
      };
    },
    // 搜索数组
    formConfigArray() {
      return [
        {
          label: this.$t('Category'),
          key: 'status',
          comp: 'el-select',
          options: this.statusOptions,
          bind: {
            clearable: true,
          },
        },
        {
          label: this.$t('Group Event'),
          key: 'status',
          comp: 'el-select',
          options: this.statusOptions,
          bind: {
            clearable: true,
          },
        },
        {
          label: this.$t('Event'),
          key: 'status',
          comp: 'el-select',
          options: this.statusOptions,
          bind: {
            clearable: true,
          },
        },
        {
          formItemClass: 'form-special-date-box', // 需要这个指定样式，在表单组件中有实现
          sameGroup: true, // 两个组件在同一个formItem中
          label: this.$t('Time (Data within 31 days)'),
          prop: 'processTime',
          comp: [
            {
              key: 'processTime',
              comp: 'el-date-picker',
              bind: {
                align: 'right',
                type: 'datetimerange',
                format: 'dd/MM/yyyy HH:mm:ss',
                'range-separator': '~',
                'value-format': 'yyyy-MM-dd HH:mm:ss',
                'default-time': ['00:00:00', '23:59:59'],
                'start-placeholder': 'Please select',
                'end-placeholder': 'Please select',
              },
            },
            {
              comp: 'i',
              class: 'el-icon-date data_icon',
            },
          ],
        },
      ];
    },
  },
  created() {
    this.customerForm.status = this.statusOptions[0].value;
  },
  methods: {
    getCustomerTableData() {
      return new Promise((resolve) => {
        lookUpBeneficiary(this.identityID)
          .then((res) => {
            if (res.header && res.header.success) {
              res.totalRecords = res.total;
            }
            let customerlist = res.customerBeneficiaryList;
            let response = {
              header: {
                success: true,
              },
              list: customerlist || [],
            };
            resolve(response);
          })
          .catch(() => {
            resolve();
          })
          .finally(() => {
            this.refreshTableFlag = false;
            this.searchLoading = false;
          });
      });
    },
    getOrganizationTableData() {
      return new Promise((resolve) => {
        lookUpBeneficiary(this.identityID)
          .then((res) => {
            if (res.header && res.header.success) {
              res.totalRecords = res.total;
            }
            let organizationlist = res.organizationBeneficiaryList;

            let response = {
              header: {
                success: true,
              },
              list: organizationlist || [],
            };
            resolve(response);
          })
          .catch(() => {
            resolve();
          })
          .finally(() => {
            this.refreshTableFlag = false;
            this.searchLoading = false;
          });
      });
    },
  },
};
</script>
<style scoped lang="scss">
.audit-log-page {
  background: $--fill_color--regular;
  .basicDec {
    background: $--text_color--white;
    color: $--line_color--strong;
    font-size: 14px;
    line-height: 22px;
    padding: 0 20px;
  }
  .table-search-container {
    box-shadow: none;
  }
  .section-title {
    font-size: 14px;
    margin-top: 20px;
    margin-left: 10px;
    margin-bottom: 20px;
    color: $--fill_color--emphasis;
    font-weight: 700;
    &::before {
      content: '';
      position: absolute;
      left: 20px;
      border: 1px solid $--status_color--notice;
      box-sizing: border-box;
      display: block;
      height: 14px;
      background-color: $--status_color--notice;
    }
  }
  .table-list-container {
    border-radius: 4px;
  }
}
</style>
