<template>
  <div class="customer-detail-page">
    <div class="header-content">
      <svg-icon icon-class="customer" class="title-img"></svg-icon>
      <div class="titles">
        <div class="title-main">{{ basicInfoForm.publicName }}</div>
        <div class="title-light">
          <span
            >{{ $t('Identity Status') }} : <span style="color: #5c5c5c">{{ datilStatus }}</span></span
          >
          <span style="margin-left: 14px">
            <el-link v-if="false" :underline="false" icon="el-icon-edit" @click="editIdentityVisibleFn"></el-link>
          </span>
        </div>
      </div>
      <div class="right-operation operation-position">
        <el-select
          :style="!customerType || customerType === '1002' ? `visibility: hidden` : ''"
          id="upgradeValue"
          value="upgrade"
          @change="upgradeSelectChange"
          :popper-append-to-body="false"
          size="small"
          class="operation-select"
          popper-class="operation-select-option"
        >
          <template slot="prefix">
            <span class="select-prefix">
              <i class="el-icon-upload2"></i>
            </span>
          </template>
          <el-option v-for="item in upgradeOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </div>
      <el-button
        v-if="false"
        @click="createGroupFn()"
        icon="el-icon-plus"
        size="small"
        type="button"
        class="createGroup"
      >
        {{ $t('IDC.orgCustomer.createGroup') }}
      </el-button>
    </div>
    <!-- title -->
    <div class="panel">
      <div class="content">
        <el-tabs v-model="activeName">
          <el-tab-pane :label="$t('IDC.groupDetail.label.info')" name="first">
            <info
              :basicInfoForm.sync="basicInfoForm"
              :identityID.sync="identityID"
              :isShowVerify.sync="isShowVerify"
              :languageName.sync="languageName"
            ></info>
          </el-tab-pane>
          <el-tab-pane :label="$t('IDC.groupDetail.label.AuditLog')" name="second">
            <audit-log :identityId="identityID" :initiatorType="initiatorType"></audit-log>
          </el-tab-pane>
          <el-tab-pane :label="$t('IDC.orgCustomer.beneficiaryList')" name="third">
            <beneficiary-list :basicInfoForm.sync="basicInfoForm" :identityID.sync="identityID"></beneficiary-list>
          </el-tab-pane>
          <el-tab-pane :label="$t('IDC.orgCustomer.groupList')" name="fourth">
            <group-list :identityID.sync="identityID"></group-list>
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
    <edit-dialog
      :dialogType="dialogType"
      :isVisible="editIdentityVisible"
      @closeIdentityStatusDialog="closeIdentityStatusDialog"
    ></edit-dialog>
  </div>
</template>

<script>
import info from './components/info.vue';
import AuditLog from '@/views/identifier-center/pages/organization-operator/detail/audit-log/index.vue';
import beneficiaryList from './components/beneficiary-list';
import groupList from './components/group-list';
import { queryCustomerInfo, queryLanguage } from '../../../api/api-customer';
import { inputRequiredValidateObj } from '@/common/validator/common';
import EditDialog from './components/edit-dialog.vue';
import dayjs from 'dayjs';
import { STATUS_MAP } from '@/views/task-center/page/task-detail/utils/data-dic.js';

export default {
  name: 'groupDetail',
  components: {
    info,
    AuditLog,
    beneficiaryList,
    groupList,
    EditDialog,
  },
  data() {
    return {
      titleConfig: {
        title: '',
        desc: this.$t('IDC.groupDetail.pageDesc'),
      },
      submitLoading: false,
      basicInfoForm: {}, // 详情接口数据
      isShowVerify: false,
      activeName: 'first', // 选项卡
      editIdentityForm: {
        identityStatus: '',
        reason: '',
        remark: '',
      },
      upgradeOptions: [
        {
          label: this.$t('IDC.orgCustomer.upgradeRegistered'),
          value: 'registered',
        },
      ],
      demoSelectData: [
        { value: '10', label: 'New CI' },
        { value: '30', label: 'New Version' },
        { value: '60', label: 'Deactive' },
      ],
      editIdentityVisible: false,
      editIdentityRules: {
        identityStatus: [inputRequiredValidateObj],
        remark: [inputRequiredValidateObj],
      },
      dialogType: '',
      listData: '', // 列表数据
      datilStatus: '', // 详情状态
      identityID: '',
      languageName: '',
      initiatorType: 'Customer',
    };
  },
  created() {
    let params = this.$route.query.params;
    this.listData = (params && JSON.parse(decodeURIComponent(params))) || '';
    this.identityID = this.listData.identityID;
    this.getCustomerInfoFn();
  },
  deactivated() {
    this.basicInfoForm = {};
  },
  computed: {
    customerType() {
      return (this.basicInfoForm && this.basicInfoForm.customerType) || '';
    },
    basePath() {
      const type = this.getConfigRouteType();
      return `/config/${type}/commissionDistribution/`;
    },
  },
  methods: {
    upgradeSelectChange(val) {
      if (val === 'registered') {
        let params = {
          identityId: this.identityID || '',
          upgradeMsisdn: this.basicInfoForm.msisdn || '',
        };
        this.openNewWindow({ path: 'create', query: { params: encodeURIComponent(JSON.stringify(params)) } });
      }
    },
    getCustomerInfoFn() {
      return new Promise((resolve) => {
        // 入参 {customerId: "1011001100000089815", cpId: "1001"}
        let params = {};
        // 注意：临时代码
        params.cpId = this.listData.cpId;
        params.customerId = this.listData.identityID;
        queryCustomerInfo(params)
          .then((res) => {
            if (res.header && res.header.success) {
              res.totalRecords = res.total;
            }
            this.basicInfoForm = { ...res };
            this.basicInfoForm.registerTime = dayjs(this.basicInfoForm.registerTime, 'DD-MM-YYYY hh:mm:ss').format(
              'DD/MM/YYYY'
            );
            this.datilStatus = this.showStatus(this.basicInfoForm.status);
            // 只有Suspended 04状态会有这块Verify Info字段显示。
            if (this.basicInfoForm.status === '05') {
              this.isShowVerify = true;
            }
            // 语言接口
            queryLanguage(params)
              .then((re) => {
                if (re.header && re.header.success) {
                  let lang;
                  lang = re.locales;
                  for (let key in lang) {
                    if (lang[key].id === this.basicInfoForm.customerBasicKyc.languageCode) {
                      this.languageName = lang[key].name;
                    }
                  }
                }
                resolve(re);
              })
              .catch(() => {})
              .finally(() => {});
            resolve(res);
          })
          .catch(() => {})
          .finally(() => {
            this.refreshTableFlag = false;
            this.searchLoading = false;
          });
      });
    },
    showStatus(val) {
      return STATUS_MAP[val] || '-';
    },

    handleEditIdentity() {},
    editIdentityVisibleFn() {
      this.dialogType = 'identity';
      this.editIdentityVisible = true;
    },
    closeIdentityStatusDialog(data) {
      this.editIdentityVisible = data;
    },
  },
};
</script>

