<script>
import PortalDialog from '@/components/portal-dialog';
import { inputRequiredValidateObj, checkRequiredValidateObj } from '@/common/validator/common';
export default {
  name: 'EditDialog',
  components: { PortalDialog },
  props: {
    isVisible: {
      type: Boolean,
      default: false,
    },
    dialogType: {
      type: String,
      default: '',
    },
    basicInfoForm: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return {
      confirmText: this.$t('common.button.submit'),
      submitLoading: false,
      currentPannel: this.$t('IDC.organizationOperatorDetail.label.KYCInfo'), // 当前tab的激活项
      demoSelectData: [
        { value: '10', label: 'New CI' },
        { value: '30', label: 'New Version' },
        { value: '60', label: 'Deactive' },
      ],
      editIdentityVisible: false,
      editIdentityForm: {
        identityStatus: '',
        reason: '',
        remark: '',
      },
      editIdentityRules: {
        identityStatus: [inputRequiredValidateObj],
        remark: [inputRequiredValidateObj],
      },
      editRegisteredbyVisible: false,
      authenticationTypeForm: {
        currentAuthenticationType: '',
        authenticationType: [],
        userName: '',
        operatorID: '',
        remark: '',
      },
      authenticationTypeFormRules: {
        authenticationType: [checkRequiredValidateObj],
        remark: [inputRequiredValidateObj],
      },
      editRuleProfileVisible: false,
      ruleProfileOptions: [],
      editRuleProfileForm: {
        remark: '',
        ruleProfile: '',
      },
      editRuleProfileRules: {
        remark: [inputRequiredValidateObj],
      },
      newLanguageOptions: [],
      editLanguageVisible: false,
      editLanguageForm: {
        oldLanguage: '',
        languageCode: '',
        remark: '',
      },
      editLanguageFormRules: {
        remark: [inputRequiredValidateObj],
      },
      editMSISDNVisible: false,
      editMSISDNForm: {
        MSISDN: '',
        newMSISDN: '',
        remark: '',
      },
      editMSISDNRules: {
        newMSISDN: [inputRequiredValidateObj],
        remark: [inputRequiredValidateObj],
      },
    };
  },
  created() {
    this.loadLanguages();
  },
  activated() {},
  deactivated() {
    this.basicInfoForm = {};
  },
  computed: {
    basePath() {
      const type = this.getConfigRouteType();
      return `/config/${type}/commissionDistribution/`;
    },
  },
  watch: {
    isVisible: {
      handler(newValue) {
        switch (this.dialogType) {
          case 'identity':
            this.editIdentityVisible = newValue;
            break;
          case 'language':
            this.editLanguageVisible = newValue;
            break;
          case 'ruleProfile':
            this.editRuleProfileVisible = newValue;
            break;
        }
      },
      immediate: true,
    },
  },
  methods: {
    async getDetail() {
      this.submitLoading = true;
      this.submitLoading = false;
    },
    // 获取语言
    loadLanguages() {},
    closeIdentityStatusDialog() {
      this.editIdentityVisible = false;
      this.$emit('closeIdentityStatusDialog', false);
      this.$refs['editIdentity'].resetFields();
    },
    closeAuthenticationTypeDialog() {
      this.editRegisteredbyVisible = false;
      this.$refs['authenticationFormRef'].resetFields();
    },
    closeEditLanguageDialog() {
      this.editLanguageVisible = false;
      this.$emit('closeEditLanguageDialog', false);
      this.$refs['editLanguageFormRef'].resetFields();
    },
    closeEditRuleProfileDialog() {
      this.editRuleProfileVisible = false;
      this.$emit('closeEditRuleProfileDialog', false);
      this.$refs['editRuleProfileForm'].resetFields();
    },
    closeMSISDNDialog() {
      this.editMSISDNVisible = false;
      this.$refs['editMSISDNForm'].resetFields();
    },
    handleEditIdentity() {},
    handleEditAccessChannel() {},
    handleEditRuleProfile() {},
    handleEditLanguage() {},
    handleEditMSISDN() {},

    /**
     * 返回操作员列表
     */
    toReturn() {
      this.$tagTab.closeCurrentPage();
    },
  },
};
</script>

