<template>
  <div class="audit-log-page">
    <div class="basicDec">
      You can query customers based on customer information and select a record in the query result to access the
      customer management page.
    </div>
    <div class="table-list-container">
      <div class="section-title">Group List</div>
      <!-- 列表 -->
      <table-page
        class="margin-top-16"
        size="small"
        :has-pagination="true"
        :isSearch="isSearch"
        :columns="customerColumns"
        :pagination="paginationConfig"
        :getTableData="getTableData"
        :sortArr="['groupCode', 'groupName', 'status', 'role']"
      >
        <template slot-scope="scope" slot="status">
          <!-- status -->
          <div class="flex flex-align">
            <span
              v-if="scope.data.row.status !== ''"
              class="status-circle"
              :style="statusStyle(scope.data.row.status)"
            ></span>
            <span>{{ showStatus(scope.data.row.status) }}</span>
          </div>
        </template>
        <template slot-scope="scope" slot="operation">
          <el-button
            size="mini"
            type="text"
            class="table-operation-btn icon-cursor"
            @click="handleDetail(scope.data.row)"
          >
            {{ $t('common.button.detail') }}
          </el-button>
        </template>
      </table-page>
    </div>
  </div>
</template>

<script>
import TablePage from '@/components/tablePage';
import { queryCustomerGroup } from '@/views/identifier-center/api/api-customer.js';

export default {
  name: 'GroupList',
  components: {
    TablePage,
  },
  props: {
    identityID: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      paginationConfig: {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
      },
      customerColumns: [
        {
          label: this.$t('Group Code'),
          prop: 'groupCode',
          align: 'left',
          'sort-method': (a, b) => {
            return a.groupCode > b.groupCode ? 1 : -1;
          },
        },
        {
          label: this.$t('Group Name'),
          prop: 'groupName',
          align: 'left',
          'sort-method': (a, b) => {
            return a.groupName > b.groupName ? 1 : -1;
          },
        },
        {
          label: this.$t('Status'),
          prop: 'status',
          __slotName: 'status',
          'sort-method': (a, b) => {
            return a.status > b.status ? 1 : -1;
          },
          align: 'left',
        },
        {
          label: this.$t('Role'),
          prop: 'role',
          align: 'left',
          'sort-method': (a, b) => {
            return a.role > b.role ? 1 : -1;
          },
        },
        {
          label: this.$t('common.label.operation'),
          prop: 'operation',
          align: 'left',
          __slotName: 'operation',
          width: 200,
        },
      ],
      isSearch: false,
    };
  },
  computed: {
    statusStyle() {
      return (status) => {
        switch (status) {
          case '00':
            return { 'background-color': `${this.GlobalStyleVariables.backgroundColor}` };
          case '02':
            return { 'background-color': `${this.GlobalStyleVariables.backgroundColor}` };
          case '03':
            return { 'background-color': `${this.GlobalStyleVariables.statusNoticeColor}` };
          case '04':
            return { 'background-color': `${this.GlobalStyleVariables.statusColorwarning}` };
          case '05':
            return { 'background-color': `${this.GlobalStyleVariables.statusColorwarning}` };
          case '06':
            return { 'background-color': `${this.GlobalStyleVariables.statusDarkBackgroundColor}` };
          case '07':
            return { 'background-color': `${this.GlobalStyleVariables.statusColorwarning}` };
          case '08':
            return { 'background-color': `${this.GlobalStyleVariables.statusColorwarning}` };
          default:
            return;
        }
      };
    },
  },
  created() {},
  methods: {
    showStatus(status) {
      switch (status) {
        case '00':
          return 'New';
        case '02':
          return 'Pending Active';
        case '03':
          return 'Active';
        case '04':
          return 'Suspended';
        case '05':
          return 'Frozen';
        case '06':
          return 'Closed';
        case '07':
          return 'Capped';
        case '08':
          return 'Dormant';
        default:
          return;
      }
    },
    handleDetail(row) {
      let params = {
        groupCode: row.groupCode,
        identityId: row.identityId,
      };
      this.openNewWindow({
        path: '/mainPage/identifierCenter/organizationInfo/group/detail',
        query: { params: encodeURIComponent(JSON.stringify(params)) },
      });
    },
    async getTableData() {
      let params = {
        receiverIdentityId: this.identityID,
        pageNo: this.paginationConfig.pageIndex,
        pageSize: this.paginationConfig.pageSize,
      };
      let res = await queryCustomerGroup(params);
      if (res.header && res.header.success) {
        var resList = res.custGroupList;
        resList.map((item, index) => {
          item.role = item.roleNameList.join(', ');
          item.status = item.groupStatus;
          item.identityId = res.groupList[index].identityId;
        });
      }
      this.isSearch = false;
      return {
        header: {
          success: 'success',
        },
        list: resList,
        totalRecords: Number(res.total) || 0,
      };
    },
  },
};
</script>
