    box-shadow: none;
    .status-circle {
      width: 6px;
      height: 6px;
      border-radius: 100%;
      display: inline-block;
      margin-right: 4px;
    }
  }
  .section-title {
    font-size: 14px;
    margin-top: 20px;
    margin-left: 10px;
    margin-bottom: 20px;
    color: $--fill_color--emphasis;
    font-weight: 700;
    &::before {
      content: '';
      position: absolute;
      left: 20px;
      border: 1px solid $--status_color--notice;
      box-sizing: border-box;
